import MicroModal from "micromodal";
import {DateTime} from 'luxon';
import "regenerator-runtime/runtime";
// import {DatePicker} from 'kintone-ui-component/lib/date-picker';


interface KintoneEvent {
  record: kintone.types.SavedFields;
  error: string[];
}

type KintoneFieldNode = {
  code: string
  label: string
}

// htmlから呼び出す仕掛け
interface GlobalFunc {
  change_rule(rule: string, weekend: string): void;
  run(src: string, dst: string, value_dst: string, label_dst: string, radio_rules: HTMLInputElement[]): void;
}

declare global {
  interface Window {
    globalFunc: GlobalFunc;
  }
}

// ボタン押下時の処理開始
const globalFunc: GlobalFunc = (() => {

  const settings = {
    limit_count: 500,            // 一度に取得する件数
  };

  const get_records : any = (_params: { [key: string]: any; } = {}) =>{
    // const MAX_READ_LIMIT = 500;

    const params = _params || {};
    const app = params.app || kintone.app.getId();
    const filterCond = params.filterCond;
    const sortConds = params.sortConds || ['$id asc'];
    const fields = params.fields;
    let data = params.data;
  
    if (!data) {
      data = {
        records: [],
        lastRecordId: 0
      };
    }

    const conditions = [];
    const limit = settings.limit_count;
    if (filterCond) {
      conditions.push(filterCond);
    }
  
    conditions.push('$id > ' + data.lastRecordId);

    const sortCondsAndLimit = ' order by ' + sortConds.join(', ') + ' limit ' + limit;
    const query = conditions.join(' and ') + sortCondsAndLimit;
    const body = {
      app: app,
      query: query,
      fields: []
    };

    if (fields && fields.length > 0) {
      // $id で並び替えを行うため、取得フィールドに「$id」フィールドが含まれていなければ追加します
      if (fields.indexOf('$id') <= -1) {
        fields.push('$id')
      }
      body.fields = fields;
    }

    return kintone.api(kintone.api.url('/k/v1/records.json', true), 'GET', body).then(function(r) {
      data.records = data.records.concat(r.records);
      if (r.records.length === limit) {
        // 取得レコードの件数が limit と同じ場合は、未取得のレコードが残っている場合があるので、getRecords を再帰呼び出して、残りのレコードを取得します
        data.lastRecordId = r.records[r.records.length - 1].$id.value;
        return get_records({
          app:app, 
          filterCond:filterCond, 
          sortConds: sortConds, 
          fields:fields, 
          data:data 
        });
      }
      delete data.lastRecordId;
      return data;
    });
  }

  const is_invalid_date_string = function(date_string : string, label: string = "変換後フィールド"){
    if(date_string === undefined || date_string === ""){
      alert(`${label}が空欄です。`)
      return true
    }
    else if(!Utils.is_date_string(date_string)){
      alert(`'yyyy-MM-dd'形式で日付文字列を入力してください。[${date_string}]`)
      return true
    }
    return false
  }

  const is_unchekecd_convert_rule = function(rules: HTMLInputElement[]){
    // const node_rule = radio_rules.find((r)=> r.checked === true )
    const node_rule = ((rules) => {
      for(const rule of rules){
        if(rule.checked === true){
          return rule
        }
      }
    })(rules)

    if(node_rule === undefined){
      // エラー表示
      alert('変換ルールを選択してください。')
      return true
      
    }
    return false
  }

  // returnにはグローバルから呼び出せる関数を定義する
  return {
    // 変換ルール選択ラジオボタンのchangeイベント
    change_rule: (convert_rule: string, weekend_rule: string) => {
      // 変換元日付の取得
      const node_src : HTMLInputElement = document.getElementById('input_source_date') as HTMLInputElement
      const date_src = node_src.value

      // 各種エラーチェック
      if(
        is_invalid_date_string(date_src)
        ){
          return
        }

      const date_converted = Utils.convert_by(convert_rule, date_src, weekend_rule)

      // 変換後日付の記入
      const node_dst : HTMLInputElement = document.getElementById('input_destination_date') as HTMLInputElement
      node_dst.value = date_converted
      node_dst.disabled = false;
    },

    // 書き込むボタン(#button-update-all)のonclickイベント
    run: async (code_src: string, code_dst: string, value_dst: string, label_dst: string, radio_rules: HTMLInputElement[]) => {

      // 各種エラーチェック
      if(
        is_invalid_date_string(value_dst, "期限日")
        ||
        is_unchekecd_convert_rule(radio_rules)
        ){
          return
        }

      var params = {
        app: kintone.app.getId(),
        // filterCond: '確度 in ("A") and 見込み時期 >= THIS_YEAR()',
        // ソート条件は、「'フィールドコード ascまたはdesc'」という形式で指定します
        // sortConds: ['$id asc', '小計 desc', '会社名 asc'],
        fields: ['レコード番号', code_src, code_dst],
        filterCond: kintone.app.getQueryCondition()
      };

      const resp_records = await get_records(params)
      const count_records = resp_records.records.length

      // ボタンの表記を書き換えてプログレスバー代わりにする
      const btn_update = document.getElementById('button-update-all')
      if(btn_update === null){
        console.log('[ERROR] プラグインに不備があります。書き込むボタンが見つかりませんでした。')
        return
      }

      // 取得したレコードを同期的ループで書き換え
      let count = 0;
      for(let r of resp_records.records){
        count += 1;
        const progress = `${count} / ${count_records}`
        btn_update.innerText = progress

        // kintoneへPUT
        const record_id = r.$id.value;
        const body = {
          'app': kintone.app.getId(),
          'id': record_id,
          'record': {
            [code_dst]: {
              'value': value_dst
            }
          }
        };
        
        try{
          await kintone.api(kintone.api.url('/k/v1/record.json', true), 'PUT', body)
        } catch(error) {
          // error
          console.log(`Error: レコードID: ${record_id} の書き込みでエラーが発生しました。(${error})`);
          alert(`書き込みでエラーが発生しました。${error}`)
          return
        }
        // success
        console.log(`Success: レコードID: ${record_id} の[${label_dst}]フィールドに[${value_dst}]を書き込みました。`);
      }
      alert(`${resp_records.records.length}件のレコードの[${label_dst}]フィールドに[${value_dst}]を書き込みました。`)
      location.reload()
    },
  };
})();
window.globalFunc = globalFunc;


/**
 * メイン処理
 */
(function (PLUGIN_ID) {
  'use strict';
  // const DEBUG_MODE = true;

  // 一覧表示イベント
  const EVENT_LIST = [
    'app.record.index.show',
  ];

  // 詳細表示イベント
  // const EVENT_SHOW = [
  //   'app.record.detail.show',
  //   'app.record.edit.show',
  //   'app.record.create.show'
  // ];

  let validator : KintoneAppValidator;
  let installor : KintoneButtonInstallor;

  // 一覧表示イベント
  kintone.events.on(EVENT_LIST, (event: KintoneEvent) => {

    const config : {} = kintone.plugin.app.getConfig(PLUGIN_ID);

    if(validator === undefined){
      validator = new KintoneAppValidator(config);
      validator.check_fields(event);
    }

    // 二重登録を回避する
    if(installor === undefined){
      installor = new KintoneButtonInstallor(config);
      installor.install(event);
    }

    // if(event.error.length > 0){
    //   event.record['cb_flg_ready']['disabled'] = true;
    // }

    return event;
  });

})(kintone.$PLUGIN_ID);


class KintoneAppValidator {
  /**
     * kintoneアプリのフィールドの設定状況をチェックします。
     * 現在定数定義にしている部分を、プラグイン化にともない設定画面からセットできるように変更する
     */

  // デフォルト値
  public static FC_DATE_REGISTER: keyof kintone.types.Fields = 'date_register'        // 変換元ファイル
  public fc_date_register : string;

  public static FC_DATE_LIMIT: keyof kintone.types.Fields = 'date_register'        // 変換元ファイル
  public fc_date_limit : string;

  constructor(config: { [x: string]: string; }){
    // configの値、またはデフォルト値を適用
    this.fc_date_register = 'date_register' in config ? config['date_register'] : KintoneAppValidator.FC_DATE_REGISTER;
    this.fc_date_limit = 'date_limit' in config ? config['date_limit'] : KintoneAppValidator.FC_DATE_LIMIT;
  }

  app_field_checker(event: KintoneEvent, fieldcode: string, label: string = "") {
    /**
     * fieldcode: 指定したフィールドコードが存在しなければkintone上にエラーメッセージを表示させる
     * ただしshow.detailではエラーは表示されない。
     * label: エラーメッセージ表示用の文字列
     */
    if(event === undefined){
      return;
    }
    if (event.error === undefined) {
      event.error = [];
    }

    if(event === undefined || event.record === undefined){
      return;
    }

    if (!(fieldcode in event.record)) {
      if(event.error.length == 0){
        event.error.push('このアプリに必要なフォームが不足しています。下記のメッセージに従い、フォームを追加してください。');
      }
      const msg = 'kintoneアプリに' + label + 'フィールド [' + fieldcode + '] がありません。';
      event.error.push(msg);
    }
  }

  check_fields(event: KintoneEvent) {
    /**
     * 設定対象となるフィールドの存在チェック
     * なければkintone上にエラーメッセージを表示する
     */
    this.app_field_checker(event, this.fc_date_register, '登録日');
    this.app_field_checker(event, this.fc_date_limit, '期限日');
    // this.app_field_checker(event, this.fc_attach_converted, '添付ファイル');
    // this.app_field_checker(event, this.fc_multiline_result, '文字列（複数行）');
    // this.app_field_checker(event, this.fc_cb_flg_ready, 'チェックボックス');
  }
}


class KintoneButtonInstallor {
  /**
   * イベントボタンを配置します
   */

  public fc_date_source : string;      // 変換元ファイルのエンコーディングを指定するラジオボタン
  public fc_date_destination : string; // 変換後ファイルのエンコーディングを指定するラジオボタン
  // public convert_rule : string; // 変換ルールを指定するラジオボタン、'couple'（翌々月末）か'follow'（翌月末）を示す
  // public convert_rule_label : string; // 変換ルールを指定するラジオボタン、'翌々月末(couple_end)'か'翌月末'(follow_end）を示す
  public convert_rules : { string : string }; // 変換ルールを指定するラジオボタンのセット、'{ label: rule_name } の辞書
  public weekend_rule : string;

  public limit_records : number; // この値よりレコード件数が多いときはボタンを無効化する

  // private convert : Function;  // 日付の変換ルール

  static MAX_PREVIEW_LINES = 5;

  constructor(config: { [x: string]: string; }){
    // configの値、またはデフォルト値を適用
    this.fc_date_source = config['fc_date_register_code']    // 変換元フィールドコード
    this.fc_date_destination = config['fc_date_limit_code']  // 変換後フィールドコード
    // this.convert_rule = config['rule_date_converting']  // 変換ルール
    // this.convert_rule_label = config['rule_date_converting_label']  // 変換ルールラベル
    this.convert_rules = JSON.parse(config['rule_date_converting']) // 利用可能な変換ルール
    this.weekend_rule = config['rule_skip_weekend']                // 週末スキップルール forward, skip, postpone

    if('number_limit_records' in config){
      this.limit_records = parseInt(config['number_limit_records'])
    }
    else{
      this.limit_records = 100;
    }

    // 変換ルールをセットする
    // this.convert = (date: string)=>{
    //   Utils.convert_by(this.convert_rule, date)
    // }
  }

  /**
   * 一括入力ダイアログを構築する
   * @param title ダイアログの見出し
   * @param label_src 入力値のラベル
   * @param label_dst 出力値のラベル
   * @param value_src 入力値文字列
   * @param value_dst 出力値文字列
   * @returns ダイアログ要素
   */
  // batchInputDialog(title: string, code_src: string, label_src: string, code_dst: string, label_dst: string, value_src: string, value_dst: string){
  async batchInputDialog(title: string, node_src: KintoneFieldNode, node_dst: KintoneFieldNode, convert_rules: { [key: string]: string}, weekend_rule: string, source_date: string){
    
    // ラジオボタンの構築
    const node_radio_convert_rules = ((convert_rules, weekend_rule)=>{
      const radio_items: string[] = []
      for(const label in convert_rules){
        const rule = convert_rules[label]
        const item = `
          <span class="kintoneplugin-input-radio-item">
            <input type="radio" name="radio_convert_rule" value="${rule}" id="${rule}" onchange="((rule, weekend_rule)=>{
              globalFunc.change_rule(rule, weekend_rule)
            })('${rule}', '${weekend_rule}')">
            <label for="${rule}">${label}</label>
          </span>
        `
        radio_items.push(item)
      }
      const node_radio = document.createElement('div')
      node_radio.className = "kintoneplugin-input-radio"
      // node_radio.id = "radio-convert-rules"
      node_radio.innerHTML = radio_items.join("")

      return node_radio
    })(convert_rules, weekend_rule);

    // 週末ルールコメントの構築
    const comment_skipping = ((rule)=>{
      let msg;
      switch(rule){
        case 'forward':
          msg = "(土日は⏪前倒し)"
          break
        case 'postpone':
          msg = "(土日は⏩先送り)"
          break
        case 'keep':
        default:
          msg = ""
      }
      return msg

    })(weekend_rule)

    // const datePicker = new DatePicker({
    //   // label: 'Date',
    //   requiredIcon: false,
    //   language: 'auto',
    //   value: '',
    //   // error: 'Error occurred!',
    //   className: 'options-class',
    //   id: 'input_destination_date',
    //   visible: true,
    //   disabled: true,
    // });
    // datePicker.className = "kintoneplugin-input-text"
    // datePicker.setAttribute('placeholder', "変換ルールを選んでください")
    // <div id="input-datepicker" class="kintoneplugin-input-outer">
    //   📅${node_dst.label}:${datePicker.outerHTML}
    // </div>

    let dialogMM = `
    <div class="modal micromodal-slide" id="modal-1" aria-hidden="true">
      <div class="modal__overlay" tabindex="-1" data-micromodal-close>
        <div class="modal__container" role="dialog" aria-modal="true" aria-labelledby="modal-1-title">
          <header class="modal__header">
            <h2 class="modal__title" id="modal-1-title">
              ${title}
            </h2>
            <button class="modal__close" aria-label="Close modal" data-micromodal-close></button>
          </header>
          <main class="modal__content" id="modal-1-content">
            <p>
            1件目の日付をルールに従って変換し、一覧表示している全レコードに一括入力します。
            </p>

            <p>
              <div class="kintoneplugin-input-outer">
                📅${node_src.label}:
                <input id="input_source_date" class="kintoneplugin-input-text" type="text" size="10" value="${source_date}" disabled="disabled">
              </div>
            </p>
            <p>
            <span class="kintoneplugin-label">⬇️変換ルール${comment_skipping}</span>
            ${node_radio_convert_rules.outerHTML}
            </p>

            <p>
              <div class="kintoneplugin-input-outer">
                📅${node_dst.label}:
                <input class="kintoneplugin-input-text" type="text" size="10" id="input_destination_date" value="" placeholder="変換ルールを選んでください" disabled>
              </div>
            </p>
            <p>
            💡日付を書き換えて一括入力することもできます。
            </p>

          </main>
          <footer class="modal__footer">
            <button id="button-update-all" class="kintoneplugin-button-dialog-ok" onclick="(()=>{
              const value_dst = document.getElementById('input_destination_date').value;
              const radio_rules = document.getElementsByName('radio_convert_rule');
              globalFunc.run('${node_src.code}', '${node_dst.code}', value_dst, '${node_dst.label}', radio_rules);
            })()">書き込む</button>
            <button class="kintoneplugin-button-dialog-cancel" data-micromodal-close aria-label="Close this dialog window">キャンセル</button>
          </footer>
        </div>
      </div>
    </div>`

    const myDiv = document.createElement("div");
    myDiv.id = 'modal-dialog-apply'
    myDiv.innerHTML = dialogMM;

    return myDiv
  }


  install(event: KintoneEvent){
    /**
     * 一括編集用のイベントボタンを配置します。
     */
    // const headerSpaceElement = kintone.app.getHeaderSpaceElement();  // レコード一覧のメニューの下側の空白部分の要素を取得する
    const headerSpaceElement = kintone.app.getHeaderMenuSpaceElement();  // レコード一覧のメニューの下側の空白部分の要素を取得する
    if(headerSpaceElement === null){
      return event;
    }

    const appid = kintone.app.getId();  // app id
    const params = {
      app: appid
    }

    const self = this;
    kintone.api(kintone.api.url('/k/v1/app/form/fields.json', true), 'GET', params, async function(resp) {
      const node_src = resp.properties[self.fc_date_source]
      const node_dst = resp.properties[self.fc_date_destination]
      if(node_src === undefined || node_dst === undefined){
        alert('設定に不備があります。プラグインの設定をやり直してください。')
        return
      }

      // 任意のスペースフィールドにボタンを設置
      const batchButton = self.createBatchButton(`${node_dst.label}を一括登録`);
      batchButton.title = `対象のレコード件数を${self.limit_records}未満に絞り込んでください。`
      batchButton.className = "kintoneplugin-button-disabled"
      
      // 絞り込み後のレコード
      const shallowRecords = await self.shallowRecords(node_src.code);

      // レコード件数が1～設定値の場合に一括入力を有効化する
      const totalCount: number = parseInt(shallowRecords.totalCount)
      if(0 < totalCount && totalCount <= self.limit_records){
        batchButton.disabled = false
        batchButton.className = "kintoneplugin-button-normal"
      }
      headerSpaceElement.appendChild(batchButton);

      // 1件目の変換元日付文字列を取得
      const date_src = ((code) => {   
        if(shallowRecords.records.length > 0){
          return shallowRecords.records[0][code].value
        }
        return ''
      })(node_src.code)
      
      // ダイアログ構築と設置
      const title = `BBk日付一括入力: ${node_dst.label}`
      const dialog = await self.batchInputDialog(title, node_src, node_dst, self.convert_rules, self.weekend_rule, date_src);  // title, label_dst, converted_date
      if(dialog == null){
        return
      }
      document.getElementsByTagName('html')[0].appendChild(dialog);

      MicroModal.init({
        disableScroll: true,
        awaitOpenAnimation: true,
        awaitCloseAnimation: true
      });

    }, function(resp) {
      // error:エラーの場合はメッセージを表示する
      var errmsg = 'レコード取得時にエラーが発生しました。';
      // レスポンスにエラーメッセージが含まれる場合はメッセージを表示する
      if (resp.message !== undefined) {
        errmsg += '\n' + resp.message;
      }
      alert(errmsg);
    });
  }

  // 絞り込みレコードを取得する
  async shallowRecords(code_src: string){
    const query = kintone.app.getQueryCondition()
    const body = {
      app: kintone.app.getId(),
      query: query,
      fields: ['$id', code_src],
      totalCount: true
    };

    let result;
    try{
      result = await kintone.api(kintone.api.url('/k/v1/records.json', true), 'GET', body)
    } catch(error){
      alert(`shallowRecords(): kitnoneの呼び出しでエラーが発生しました。${error}`)
      return null
    }
    return result
  }

  // 一括入力ダイアログを表示するボタン
  createBatchButton(label: string){
    const btn = document.createElement('button');
    btn.setAttribute('class', 'kintoneplugin-button-disabled') // ボタンスタイル
    btn.setAttribute('disabled', 'true') // デフォルトでは不活性
    btn.setAttribute('data-micromodal-trigger', 'modal-1')
    btn.id = 'my_space_field_button';
    btn.innerText = label;
    return btn
  }
}

// 日付変換ユーティリティ
class Utils {

  static readonly WEEKEND_KEEP: string = 'keep'
  static readonly WEEKEND_FORWARD: string = 'forward'
  static readonly WEEKEND_POSTPONE: string = 'postpone'

  /**
   * 
   * @param rule ルール文字列、this_end, follow_end, follow_15th, couple_end, current_day
   * @param date 日付文字列、ハイフン区切りのYYYY-MM-DDを想定
   * @param weekend 週末に掛かった場合の扱い、forward, keep, postponeのいずれか
   * @returns 
   */
  // static convert_by(rule: string, date: string, weekend: string = 'keep'){
    static convert_by(rule: string, date: string, weekend: string = Utils.WEEKEND_KEEP){
    switch(rule){
      case 'this_end':    // 当月末
        return Utils.convert_this_end(date, weekend)
      case 'follow_15th':  // 翌月15日
        return Utils.convert_follow_15th(date, weekend)
      case 'later_30days':  // 30日後
        return Utils.convert_later_30days(date, weekend)
      case 'follow_end':  // 翌月末
        return Utils.convert_follow_end(date, weekend)
      case 'couple_end':  // 翌々月末
        return Utils.convert_couple_months_end(date, weekend)
      case 'current_day':  // 当日
      default:  // そのまま帰す
        return date
    }
  }

  // 日付として正しい文字列かどうかをチェック
  static is_date_string(str: string){
    const dt = DateTime.fromFormat(str, 'yyyy-MM-dd')
    return dt.isValid
  }

  // 当月末
  static convert_this_end(date_from: string, weekend = Utils.WEEKEND_KEEP){
    return this.end_of_month(date_from, 0, weekend)
  }

  // 翌月15日
  static convert_follow_15th(date_from: string, weekend=Utils.WEEKEND_KEEP){
    return this.end_of_month(date_from, 1, weekend, 15)
  }

  // 30日後
  static convert_later_30days(date_from: string, weekend=Utils.WEEKEND_KEEP){
    const dt = this.dateFormat(date_from)
    const new_dt = dt.plus({days: 30})

    const date_slided = Utils.slide_with_weekends(new_dt, weekend)
    return date_slided.toFormat('yyyy-MM-dd')
  }
  
  // 翌月末
  static convert_follow_end(date_from: string, weekend=Utils.WEEKEND_KEEP){
    return this.end_of_month(date_from, 1, weekend)
  }

  // 翌々月末
  static convert_couple_months_end(date_from: string, weekend=Utils.WEEKEND_KEEP){
    return this.end_of_month(date_from, 2, weekend)
  }

  /**
   * 月末の日付を求める。変換後の日付が週末だった場合に指定した変化をさせる
   * @param date 基準日の文字列（yyyy-MM-dd形式）
   * @param add_months 前後する月数
   * @param weekend 変換後が週末だった場合の扱い（"keep": 変化なし / "postpone": 後ろ倒し / "forward": 前倒し）
   * @param day 日付の指定、0や月ごとの日数を超える場合は末日として扱う。デフォルトは0（末日）
   * @returns 
   */
  static end_of_month(date: string, add_months: number = 0, weekend: string = Utils.WEEKEND_KEEP, day: number = 0){
    const dt = this.dateFormat(date)
    const end = ((add_mon, day) => {  // 指定した月の末日、または指定した日を返す
      let new_dt = dt.plus({months: add_mon}).endOf('month')
      if(1 < day && day < new_dt.endOf('month').day ){
        new_dt = new_dt.set({day: day})
      }
      return new_dt
    })(add_months, day)

    const date_slided = Utils.slide_with_weekends(end, weekend)
    return date_slided.toFormat('yyyy-MM-dd')
  }

  static slide_with_weekends(end: DateTime, weekend: string = Utils.WEEKEND_KEEP){
    const dow = end.weekday; // day of week

    let move_days = 0

    if(weekend == Utils.WEEKEND_POSTPONE){     // スキップ
      if(dow == 6){  // 土曜日
        move_days = 2
      }
      else if(dow == 7){  // 日曜日
        move_days = 1
      }

    }
    else if(weekend == Utils.WEEKEND_FORWARD){  // 前倒し
      if(dow == 6){  // 土曜日
        move_days = -1
      }
      else if(dow == 7){  // 日曜日
        move_days = -2
      }
    }
    return end.plus({day: move_days})
  }

  static dateFormat(date: string){
    return DateTime.fromFormat(
      date,
      "yyyy-LL-dd"
    );
  }

}
